.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 50px;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    overflow-y: scroll;
    transition: 0.2s;
    color:#777a80;
    font-weight: bolder;
    scroll-behavior: smooth;
  }

  .sidenav a {
  text-decoration: none;
  color: #818181;
  display: block;
  transition: 0.3s;
  padding-bottom: 1px;
  font-size: initial;
}

.sidenav a:hover {
  color: #f1f1f1;
}

.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

.sidenav tr td{
    padding-left: 5vw;
}
.sidenav tr{
    margin-bottom: 1vw;
}

#mySidenav{
    transition: 0.3s; 
}
